.local1857-voice-editor-news {
	padding: 1rem;

	.local1857-recent-news-editor-heading {
		font-size: 0.75rem;
		color: var(--wp--preset--color--kcls-voice-black);
	}

	img {
		object-fit: contain;
		width: 100%;
		max-height: 150px;
	}

	p {
		font-size: 0.5rem;
	}
}

.local1857-editor-news-overlay {
	background: rgba(0, 0, 0, 0.8);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.local1857-voice-editor-news-container {
	display: flex;
}

.local1857-loading-posts {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;

	h2 {
		text-align: center;
	}
}
